<template>
    <nav>
        <router-link to="/">Home</router-link> | <router-link to="/about">About</router-link> |
        <router-link to="/kakaologin">Kakao Login</router-link> |
        <router-link to="/naverlogin">Naver Login</router-link> |
        <router-link to="/googlelogin">Google Login</router-link>
    </nav>

    <router-view />

    <div id="app">
        <h1>통합 로그인 데모</h1>

        <button @click="kakaoLogin">카카오 로그인</button>
        <div id="naverIdLogin"></div>
        <GoogleLogin :callback="handleGoogleLogin" prompt />
    </div>
</template>

<script setup>
import { onMounted } from 'vue';
import { GoogleLogin } from 'vue3-google-login';

onMounted(() => {
    if (window.Kakao && !window.Kakao.isInitialized()) {
        window.Kakao.init('YOUR_KAKAO_JAVASCRIPT_KEY');
    }

    const naverLogin = new window.naver.LoginWithNaverId({
        clientId: 'YOUR_NAVER_CLIENT_ID',
        callbackUrl: 'https://kimdohyeon0532.github.io/login_test/',
        isPopup: true,
        loginButton: { color: 'green', type: 3, height: 50 }
    });
    naverLogin.init();
});

const kakaoLogin = () => {
    window.Kakao.Auth.login({
        success: function (authObj) {
            console.log('카카오 로그인 성공', authObj);
        },
        fail: function (err) {
            console.error('카카오 로그인 실패', err);
        }
    });
};

const handleGoogleLogin = (response) => {
    console.log('구글 로그인 성공', response);
};
</script>

